/* Global Styles */

html,body {
  background:#e9e9e9;
  color:#333;
  font-family:"Work Sans", sans-serif;
  line-height:1.4;
  height: 100%;
  width: 100%;
  text-align: center;
}

.FirstClass {
  color: #bbb;
}

.General {
  width: 60%;
  margin: 0 auto;
}

/* CRUD classes */
.crud-header-zone {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.crud-header-zone * {
  margin: 20px;
}

.crud-content-zone {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.crud-content-tile {
  display:block;
  position: relative;
  min-height: 100px;
  margin: 50px;
  width: 150px;
  border: 1px solid #000;
  border-collapse: collapse;
  border-radius: 3px;
}

.crud-footer-zone {
  width: 500px;
  margin: 0 auto;
  display:block;
  position: relative;
}

.crud-textarea {
  border: 1px solid #000;
  border-collapse: collapse;
  width: 100%;
  min-height: 75px;
}

.crud-textarea-label {
  text-align: left;
}

/* .crud-add-button {} */

.crud-del-sign {
  position: absolute;
  width: 25px;
  height: 25px;
  top: -12px;
  right: -12px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #e9e9e9;
}

.crud-add-button {
  position: absolute;
  width: 25px;
  height: 25px;
  bottom: 12px;
  right: 12px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #e9e9e9;
}

.crud_refresh_button {
  width: 25px;
  height: 25px;
  object-fit: contain;
  background-color: #e9e9e9;
 }
